              <div class="article-list" :class="{'article-list-open': sectionIndex === showIndex}">
                <p class="article-name" v-for="(article, articleIndex) in getShowArticles(sectionIndex === showIndex, section.articles)"
                   @click="displayArticle(showIndex, articleIndex)">{{article.name}}</p>
              </div>
              <p v-if="section.articles.length > 3" class="show-more"
                 @click="openSectionOpen(catalogueIndex, showIndex)">
                <i class="show-more-icon"
                   :class="{'show-more-icon-close': showIndex === sectionIndex, 'show-more-icon-open': showIndex != sectionIndex}"
                ></i>{{section.showMoreTip}}</p>
