<script>
  import PublicHeader from '@/components/PublicHeader.vue';
  import SupportArticle from '@/components/SupportArticle.vue';
  import SupportContactUsView from '@/components/SupportContactUsView.vue';

  export default {
    name: 'support',
    data() {
      return {
        isLoading: true,
        articles: [],
        sections: [],
        categories: [], // for request buf
        catalogue: [], // for display
        breadcrumb: [],
        iconLoading: false,
        searchText: '',
        searchTextBuf: '',
        selectCatalogue: {},
        catalogueIndex: 0,
        sectionIndex: 0,
        articleBody: '',
        showType: 0, // 0: section list; 1: article; 2: search results
        searchResults: [],
      };
    },
    components: {
      PublicHeader,
      SupportArticle
    },
    watch: {
      $route(to, from) {
        if (to.name === 'support') {
          this.showType = 0;
          this.selectCatalogue = this.catalogue[0];
          this.catalogueIndex = 0;
          this.sectionIndex = 0;
        }
        // if (to.path.indexOf('article') === -1) {
        //   this.showType = 0;
        //   this.openCategory(this.catalogueIndex);
        // }
      }
    },
    created() {
      if (this.$route.path.indexOf('contact-us') > -1) {
        this.openContact();
      }
    },
    mounted() {
      const that = this;
      this.$request.getZendeskArticles(function (data) {
        if (data.status === 200) {
          const result = JSON.parse(JSON.stringify(data.data));
          that.articles = result.articles.filter(articleItem => {
            // remove all articles contain SSR
            return articleItem.title.toUpperCase().indexOf('SSR') === -1
          });
          that.$request.getZendeskCategories(function (data) {
            if (data.status === 200) {
              const result = JSON.parse(JSON.stringify(data.data));
              that.categories = result.categories.filter(categorigesItem => {
                // remove the simple guide
                return categorigesItem.id != 360002460371
              });
              // console.log(that.categories)
              that.$request.getZendeskSections(function (data) {
                if (data.status === 200) {
                  const result = JSON.parse(JSON.stringify(data.data));
                  that.sections = result.sections.filter(sectionItem => {
                    // remove the see the world
                    return sectionItem.id != 360006634511
                  });
                  // disposal data
                  that.categories.forEach(categoryItem => {
                    categoryItem['sections'] = [];
                    that.sections.forEach(sectionItem => {
                      // if (sectionItem.id != 360006634511) {
                        if (sectionItem['category_id'] === categoryItem['id']) {
                          sectionItem['articles'] = [];
                          sectionItem['showMoreTip'] = '展開' // by default
                          that.articles.forEach(article => {
                            if (article['section_id'] === sectionItem['id'] && article['id'] != 360038782191) {
                              sectionItem['articles'].push(article);
                            }
                          });
                          categoryItem['sections'].push(sectionItem);
                        }
                      // }
                    });
                    // // hide the section if the categories has only one section
                    // if (categoryItem['sections'].length === 1) {
                    //   categoryItem['articles'] = categoryItem['sections'][0]
                    // }
                  });
                  that.catalogue = that.categories;
                  that.isLoading = false;
                  that.breadcrumb = [];
                  // display the article if the route exist
                  if (that.$route.path.indexOf('article') > -1) {
                    that.showType = 1;
                    let articleId = that.$route.path.substring(that.$route.path.lastIndexOf('/') + 1, that.$route.path.length)
                    // that.$router.push({
                    //   path: 'article',
                    //   name: 'article',
                    //   params: {
                    //     articleId: articleId
                    //   }
                    // }).catch(err => console.log(err));
                    // find the section where this article belong
                    let section = that.sections.find(section => section.id === parseInt(that.articles.find(article => article.id === parseInt(articleId))['section_id']))
                    // find the catalogue where this section belong
                    that.selectCatalogue = that.catalogue.find(catalogueItem => catalogueItem.id === parseInt(section['category_id']))
                    // set the breadcrumb and the index
                    that.breadcrumb[0] = '幫助中心';
                    that.breadcrumb[1] = that.selectCatalogue.name;
                    that.breadcrumb[2] = section.name;
                    that.catalogueIndex = that.catalogue.indexOf(that.selectCatalogue)
                    that.sectionIndex = that.selectCatalogue.sections.indexOf(section)
                  } else {
                    if (that.$route.path.indexOf('contact-us') === -1) {
                      that.selectCatalogue = that.catalogue[0];
                      that.breadcrumb[0] = '幫助中心';
                      that.breadcrumb[1] = that.selectCatalogue.name;
                      that.breadcrumb[2] = that.selectCatalogue.sections[0].name;
                    } else {
                      that.breadcrumb[0] = '幫助中心';
                      that.breadcrumb[1] = '聯繫我們';
                    }
                  }
                  Array.prototype.forEach.call(that.selectCatalogue.sections, function (value, index) {
                    if (index != that.sectionIndex) {
                      value.showMoreTip = '展開';
                    } else {
                      value.showMoreTip = '收起';
                    }
                  })
                }
              });
            }
          });
        }
      });


      // This code just for open the zendesk webWidget
      // setTimeout(function () {
      //   // let test = document.getElementsByClassName('zEWidget-launcher')[0].contentWindow.document.getElementsByClassName('wrapper-AtBcr')[0].click()
      //   // console.log(test)
      //   window.zE('webWidget', 'toggle')
      //   // setTimeout(function () {
      //   //   let test = document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementById('Embed').getElementsByTagName('div')
      //   //   console.log(test)
      //   //
      //   // }, 3000)
      // }, 5000)

      // this.breadcrumb = [];
      // this.breadcrumb[0] = '幫助中心';
    },
    methods: {
      openZw() {
        window.zE('webWidget', 'open');
        setTimeout(function () {
          // Open the channel
          document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
        }, 1000);
      },
      openContact() {
        this.showType = 3;
        this.breadcrumb = [];
        this.breadcrumb[0] = '幫助中心';
        this.breadcrumb[1] = '聯繫我們';
        this.$router.push({
          path: 'contact-us',
          name: 'contact-us'
        });
      },

      getFileName(file, fileList) {
        console.log(file);
        console.log(fileList);
        return false;
        // this.uploadData.filename =
      },
      getShowArticles(isSelectedItem, items) {
        if (isSelectedItem) {
          return items;
        } else {
          return items.filter(function (value, index) {
                return index < 3;
          })
        }
      },
      openCategory(index) {
        if (!window.location.pathname.endsWith('help-center')) {
          this.$router.push({
            path: 'help-center',
            name: 'support'
          });
        }
        const indexBuf = index
        const that = this
        setTimeout(function () {
          that.showType = 0;
          that.selectCatalogue = that.catalogue[indexBuf];
          that.catalogueIndex = indexBuf;
          that.sectionIndex = 0;

          Array.prototype.forEach.call(that.selectCatalogue.sections, function (value, index) {
            if (index != that.sectionIndex) {
              value.showMoreTip = '展開';
            } else {
              value.showMoreTip = '收起';
            }
          })

          that.breadcrumb = [];
          that.breadcrumb[0] = '幫助中心';
          that.breadcrumb[1] = that.selectCatalogue.name;
        }, 10)

      },
      /**
       * Open from catalogueIndex === 0
       */
      openSection(categoryIndex, sectionIndex) {
        if (!window.location.pathname.endsWith('help-center')) {
          this.$router.push({
            path: 'help-center',
            name: 'support'
          });
        }

        const categoryIndexBuf = categoryIndex
        const sectionIndexBuf = sectionIndex
        const that = this
        // wait until router setup completed
        setTimeout(function () {
          that.showType = 0;
          that.selectCatalogue = that.catalogue[categoryIndexBuf];
          that.catalogueIndex = categoryIndexBuf;
          that.sectionIndex = sectionIndexBuf;

          Array.prototype.forEach.call(that.selectCatalogue.sections, function (value, index) {
            if (index != that.sectionIndex) {
               value.showMoreTip = '展開';
            } else {
               value.showMoreTip = '收起';
            }
          })

          that.breadcrumb[1] = that.selectCatalogue.name;
          that.breadcrumb[2] = that.selectCatalogue['sections'][sectionIndexBuf].name;
        }, 10)
      },
      /**
       * Open from catalogueIndex != 0
       */
      openSectionOpen(catalogueIndex, showIndex) {
        const that = this
        Array.prototype.forEach.call(this.catalogue[catalogueIndex].sections, function (value, index) {
          if (index != showIndex) {
            value.showMoreTip = '展開';
          } else {
            if (value.showMoreTip === '收起') {
              value.showMoreTip = '展開';
              that.sectionIndex = 1000
            } else {
              value.showMoreTip = '收起';
              that.openSection(catalogueIndex, showIndex);
            }
          }
        })
      },
      displayArticle(sectionIndex, index) {
        this.sectionIndex = sectionIndex;
        this.showType = 1;
        // this.articleBody = this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['articles'][index]['body'].replace(/whaleblue\.zendesk\.com/g, 'sg.inforun.work')
        this.$router.push({
          path: 'article',
          name: 'article',
          params: {
            articleId: this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['articles'][index]['id'],
          }
        });
        this.breadcrumb[2] = this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['name'];
        // this.breadcrumb[3] = this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['articles'][index]['name']

        // this.$route.path = this.$route.path + '/article/' + this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['articles'][index]['id']
        // console.log(this.$route)
      },
      displayFromSearch(result) {
        this.showType = 1;
        this.$router.push({
          path: 'article',
          name: 'article',
          params: {
            articleId: result.id,
          }
        });
        this.breadcrumb[1] = result.categorieName;
        this.breadcrumb[2] = result.sectionName;
        // this.breadcrumb[3] = this.catalogue[this.catalogueIndex]['sections'][this.sectionIndex]['articles'][index]['name']
      },
      gotoHome() {
        this.$router.push({
          path: 'home',
          name: 'home'
        });
      },
      breadcrumbGoto(index) {
        switch (index) {
          case 0:
            this.showType = 0;
            this.breadcrumb = this.breadcrumb.slice(0, 1);
            this.catalogueIndex = 0;
            this.selectCatalogue = this.catalogue[0];
            break;
          case 1:

            this.sectionIndex = 1000;
            this.breadcrumb = this.breadcrumb.slice(0, 2);
            break;
          case 2:

            this.breadcrumb = this.breadcrumb.slice(0, 3);
            break;
        }


      },
      searchKeyword() {
        const that = this;
        that.searchTextBuf = that.searchText;
        this.$request.getSearchKeyword(this.searchText, function (data) {
          that.searchResults = data.data.results;
          that.searchResults.forEach(function (resultItem) {
            that.sections.forEach(function (sectionItem) {
              if (resultItem['section_id'] === sectionItem.id) {
                that.categories.forEach(function (categoriesItem, categorieIndex) {
                  if (categoriesItem.id === sectionItem['category_id']) {
                    resultItem['categorieName'] = categoriesItem.name;
                    resultItem['categorieIndex'] = categorieIndex;
                    resultItem['sectionName'] = sectionItem.name;
                    resultItem['sectionIndex'] = categoriesItem.sections.indexOf(sectionItem);
                  }
                });
              }
            });
          });
          that.showType = 2;
        });
      },
    }
  };
</script>

