<template>
  <div>
    <div v-if="channel === 0" class="contact-container">
      <p class="client-title">聯繫我們</p>
      <p class="client-description">為您提供進一步幫助</p>
      <div class="channel-item-container">
        <div class="channel-item">
          <i class="channel-icon"></i>
          <p class="channel-title">實時聊天</p>
          <p class="channel-description">在您的瀏覽器中和我們實時聊天。</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openZw()">開始聊天</a-button>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-email-icon"></i>
          <p class="channel-title">提交工單</p>
          <p class="channel-description">開一個工單，填寫表單或發送郵件給我們。</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openNewRequest()">發工單</a-button>
        </div>
        <div class="channel-item">
          <i class="channel-icon channel-phone-icon"></i>
          <p class="channel-title">電話溝通</p>
          <p class="channel-description">呼叫支援專員或讓我們回撥電話。</p>
          <a-button class="subscribe-btn" type="primary" ghost @click="openZw()">開始通話</a-button>
        </div>
      </div>
    </div>
    <div v-if="channel === 1" class="contact-email">
      <p class="client-title">提交工單</p>
      <a-form class="email-form" :form="form" @submit="handleSubmit" layout="vertical">
        <a-form-item label="電郵" class="form-item">
          <a-input class="form-email" placeholder="您的電郵"
                   v-decorator="['email', { rules: [{ required: true, message: '您的電郵' }],initialValue: email }]"
          />
        </a-form-item>
        <a-form-item label="問題描述" class="form-item">
          <a-textarea class="form-description" placeholder="描述您遇到的問題" :autosize="{ minRows: 8, maxRows: 8}"
                      v-decorator="['detail', { rules: [{ required: true, message: '描述您遇到的問題' }] }]"
          />
        </a-form-item>
        <a-form-item label="附件" class="form-item">
          <div class="dropbox">
            <a-upload-dragger
                    class="form-upload-dragger"
                    v-decorator="['dragger',{valuePropName: 'fileList',getValueFromEvent: normFile,},]"
                    name="uploaded_data"
                    action="https://sg.inforun.work/api/v2/uploads"
                    :data="uploadData"
                    @change="handleChange"
                    :beforeUpload="uploadFileCheck"
            >
              <p class="ant-upload-drag-icon">
                <i class="form-upload-icon"></i>
              </p>
              <p class="ant-upload-text">
                新增檔案
              </p>
              <p class="ant-upload-hint">
                或將檔案放置到這裡 (最多5個)
              </p>
            </a-upload-dragger>
          </div>
        </a-form-item>
        <a-form-item class="form-item-btn">
          <a-button class="submit-btn" type="primary" html-type="submit" :loading="submitLoading">
            提交
          </a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'SupportContactUsView',
    data() {
      return {
        channel: 0,
        uploadData: {
          filename: ''
        },
        formLayout: 'horizontal',
        form: this.$form.createForm(this, { name: 'coordinated' }),
        uploadFIleIds: [],
        submitLoading: false,
        maxFiles: 5,
        email: localStorage.getItem("email")
      };
    },
    methods: {
      openZw() {
        window.zE('webWidget', 'open');
        setTimeout(function () {
          // Open the channel
          document.getElementsByClassName('zEWidget-webWidget--active')[0].contentWindow.document.getElementsByTagName('footer')[0].getElementsByTagName('button')[0].click();
        }, 1000);
      },
      openNewRequest() {
        this.channel = 1;
      },
      handleSubmit(e) {
        e.preventDefault();
        this.form.validateFields((err, values) => {
          if (!err) {
            this.submitLoading = true;
            // console.log('Received values of form: ', values);
            let data = {
              'request': {
                'requester': {
                  'name': values['email'],
                  'email': values['email']
                },
                'subject': values['detail'],
                'comment': {
                  'body': `${values['detail']}↵↵---------- ↵ 來自${window.location.href}`,
                  'upload': this.uploadFIleIds
                }
              }
            };
            const that = this;
            this.$request.postNewRequest(data, function (data) {
              that.submitLoading = false;
              if ((data.status === 201 || data.status === 200) && data.request.id != 0) {
                that.channel = 0;
                that.$notification.open({
                  message: '感謝您與我們聯繫\n' +
                    '相關人員會盡快回复您',
                  onClick: () => {
                  },
                });
              } else {
                that.$notification.open({
                  message: '處理您的請求時出錯。請稍後重試。',
                  onClick: () => {
                  },
                });
              }
            });
          }
        });
      },
      handleChange(info) {
        // console.log(info)
        const status = info.file.status;
        if (status === 'done') {
          this.$message.success(`${info.file.name} 上傳成功。`);
          this.uploadFIleIds.push(info.file.response.upload.token);
        } else if (!status) {
          this.$message.error(`${info.file.name} 上傳失敗。`);
        }


      },
      normFile(e) {
        if (Array.isArray(e)) {
          return e;
        }
        return e && e.fileList;
      },
      uploadFileCheck(file, fileList) {
        return this.uploadFIleIds.length < this.maxFiles;
      }
    }
  };
</script>

